import * as S3 from 'aws-sdk/clients/s3';
import { environment } from '../../../environments/environment';
import { Subject } from 'rxjs/Subject';
var UploadFileService = /** @class */ (function () {
    function UploadFileService() {
        this.FOLDER = 'user-s3/';
        this.dataList = {
            'master-image': new Subject(),
            'slave-image1': new Subject(),
            'slave-image2': new Subject(),
            'slave-image3': new Subject(),
            'slave-image4': new Subject(),
            'slave-image5': new Subject(),
            'file': new Subject()
        };
        this.bucket = new S3({
            accessKeyId: environment.bucket.accessKeyId,
            secretAccessKey: environment.bucket.secretAccessKey,
            region: environment.bucket.region
        });
    }
    UploadFileService.prototype.uploadFile = function (file, columnName) {
        var _this = this;
        var date = new Date().toISOString();
        date = date.substr(2, 8) + '/' + date.substr(11, 17);
        var params = {
            Bucket: 'we2winimage',
            Key: this.FOLDER + date + file.name,
            Body: file
        };
        this.bucket.upload(params, function (err, data) {
            if (err) {
                console.log('There was an error uploading your file: ', err);
            }
            else {
                console.log('Successfully uploaded file.', data);
                console.log(_this.dataList);
                _this.dataList[columnName].next(data);
            }
        });
    };
    UploadFileService.prototype.getFileName = function (columnName) {
        return this.dataList[columnName].asObservable();
    };
    UploadFileService.prototype.resetFile = function () {
        // tslint:disable-next-line:forin
        for (var i in this.dataList) {
            this.dataList[i] = new Subject();
        }
    };
    return UploadFileService;
}());
export { UploadFileService };



