import { HttpClient } from '@angular/common/http';
import { JwtHelper } from 'angular2-jwt';
import { environment } from '../../../environments/environment';
var UserService = /** @class */ (function () {
    function UserService(http, jwtHelper) {
        this.http = http;
        this.jwtHelper = jwtHelper;
        this.appUrl = environment.apiUrl;
    }
    UserService.prototype.getUserList = function (level, amount, id) {
        return this.http.get(environment.apiUrl + '/mng/users/' + level + '/' + amount + '/' + (id || '1'))
            .map(function (res) { return res; });
    };
    UserService.prototype.searchUser = function (query) {
        return this.http.get(environment.apiUrl + '/mng/users/' + query)
            .map(function (res) { return res; });
    };
    UserService.prototype.deleteUser = function (user) {
        var url = environment.apiUrl + '/mng/users/' + user['user_id'];
        var bodyString = JSON.stringify(user);
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.delete(url, headers)
            .map(function (res) { return res; });
        // .catch((error: any) => { console.log(error.message); });
    };
    return UserService;
}());
export { UserService };



