    ContentsModificationComponent.prototype.onSubChange = function (selected) {
        this.selectedData.type = selected;
        this.selectedData.body = {};
        this.selectBoxData = {};
        this.updateContents(selected);
        switch (selected) {
            case '아파트':
            case '오피스텔':
            case '상가/호텔':
            case '토지':
                this.siteForm.controls['s-type'].setValue(selected);
                break;
        }
        // tslint:disable-next-line:forin
        for (var i in this.uploadedFiles) {
            this.uploadedFiles[i] = '';
        }
        // tslint:disable-next-line:forin
        for (var i in this.inputs) {
            this.inputs[i] = '기존 파일';
        }
    };
    ContentsModificationComponent.prototype.updateContents = function (selected) {
        var _this = this;
        console.log('updateContents(): ', this.engType[selected], selected);
        if (selected === '오프라인 모임') {
            this.contentsService.getSimplesList(this.engType[selected], 'date').subscribe(function (data) {
                var titles = [];
                _this.contentNoArr = [];
                _this.loadedData = data;
                // tslint:disable-next-line:forin
                for (var record in _this.loadedData) {
                    titles.push(_this.loadedData[record]['title']);
                    _this.contentNoArr.push(_this.loadedData[record]['no']);
                }
                if (Object.keys(titles).length !== 0) {
                    _this.contents.categories['컨텐츠 제목'] = titles;
                    _this.contents.selected = '컨텐츠 제목';
                }
                else {
                    _this.contents.categories['컨텐츠 제목'] = [];
                    _this.contents.selected = '컨텐츠가 없습니다.';
                }
                // console.log('updated: ', titles);
            }, function (error) {
                console.log('error loading contents');
            });
        }
        else {
            this.contentsService.getContentsList(this.engType[selected], 'newly', 'date').subscribe(function (data) {
                var titles = [];
                _this.contentNoArr = [];
                _this.loadedData = data;
                // tslint:disable-next-line:forin
                for (var record in _this.loadedData) {
                    titles.push(_this.loadedData[record]['title']);
                    _this.contentNoArr.push(_this.loadedData[record]['no']);
                }
                if (Object.keys(titles).length !== 0) {
                    _this.contents.categories['컨텐츠 제목'] = titles;
                    _this.contents.selected = '컨텐츠 제목';
                }
                else {
                    _this.contents.categories['컨텐츠 제목'] = [];
                    _this.contents.selected = '컨텐츠가 없습니다.';
                }
                // console.log('updated: ', titles);
            }, function (error) {
                console.log('error loading contents');
            });
        }
    };
    ContentsModificationComponent.prototype.onContentsChange = function (num) {
        var _this = this;
        console.log('onContentsChange(): ', num, this.contentNoArr[num]);
        this.contentsService.getContentsDetail(this.engType[this.selectedData.type], this.contentNoArr[num]).subscribe(function (data) {
            _this.loadedData = data;
            console.log('changedData: ', data);
            var form = _this.forms[_this.selectedData.type];
            // const symbol = form.controls;
            // console.log(symbol);
            // tslint:disable-next-line:forin
            for (var field in form.controls) {
                if (field === 'open-start' || field === 'open-end' ||
                    field === 'apply-start' || field === 'apply-end' ||
                    field === 'duration-start' || field === 'duration-end') {
                    form.controls[field].setValue(_this.loadedData[field].slice(0, 10));
                }
                else if (field === 'level') {
                    form.controls[field].setValue(_this.loadedData[field]);
                    console.log('type: ', _this.selectedData.type);
                    switch (_this.selectedData.type) {
                        case '리포트':
                            _this.ILevel.selected = _this.loadedData[field];
                            break;
                        case '부동산 뉴스':
                            _this.NLevel.selected = _this.loadedData[field];
                            break;
                        case '법률 및 정책':
                            _this.LLevel.selected = _this.loadedData[field];
                            break;
                        case '아파트':
                        case '오피스텔':
                        case '상가/호텔':
                        case '토지':
                            _this.SLevel.selected = _this.loadedData[field];
                            break;
                        case '오프라인 모임':
                            _this.MLevel.selected = _this.loadedData[field];
                            break;
                    }
                }
                else {
                    console.log(field + ': ', _this.loadedData[field]);
                    form.controls[field].setValue(_this.loadedData[field]);
                    _this.inputs[field.slice(1)] = _this.loadedData[field];
                }
            }
        }, function (error) {
            console.log('error loading contents');
        });
    };
    ContentsModificationComponent.prototype.onLevelChange = function (_type) {
        // console.log('_type: ', _type);
        switch (_type) {
            case 'report':
                this.selectBoxData['level'] = this.ILevel.selected;
                break;
            case 'news':
                this.selectBoxData['level'] = this.NLevel.selected;
                break;
            case 'law':
                this.selectBoxData['level'] = this.LLevel.selected;
                break;
            case 'site':
                this.selectBoxData['level'] = this.SLevel.selected;
                break;
        }
    };
