    ContentsModificationComponent.prototype.selectFile = function (event, columnName) {
        this.uploadedFiles[columnName] = event.target.files.item(0);
        this.inputs[columnName] = '파일 없음';
        // console.log(this.uploadedFiles);
    };
    ContentsModificationComponent.prototype.upload = function (type, columnName) {
        var _this = this;
        console.log('type: ', type);
        var file = this.uploadedFiles[columnName];
        if (file) {
            this.uploadService.uploadFile(file, columnName);
            this.subscription = this.uploadService.getFileName(columnName)
                .map(function (data) { return data.Key; }).subscribe(function (name) {
                console.log('name: ', name);
                console.log(_this.forms[type], columnName);
                _this.forms[type].controls[columnName].setValue(name);
                _this.inputs[columnName] = name;
                console.log(_this.inputs);
                _this.uploadedFiles[columnName] = '-done';
                // this.alertService.info('업로드 되었습니다.');
            });
        }
        else {
            this.alertService.info('선택한 파일이 없습니다.');
        }
    };
    ContentsModificationComponent.prototype.uploadFile = function (type, columnName) {
    };
    ContentsModificationComponent.prototype.onSubmit = function () {
        this.selectedData.body = this.forms[this.selectedData.type].value;
        this.selectedData.body = Object.assign(this.selectedData.body, this.selectBoxData);
        console.log(this.selectedData);
        if (this.forms[this.selectedData.type].valid) {
            this.putData(this.selectedData);
        }
        else {
            this.alertService.info('양식이 모두 입력되지 않았습니다.');
        }
    };
    ContentsModificationComponent.prototype.preview = function () {
        this.selectedData.body = this.forms[this.selectedData.type].value;
        this.selectedData.body = Object.assign(this.selectedData.body, this.selectBoxData);
        console.log(this.selectedData, this.selectBoxData);
        this.putDataService.updateData(this.selectedData.body, this.selectedData.type);
        this.isDataChanged = true;
    };
    ContentsModificationComponent.prototype.putData = function (selectedData) {
        var _this = this;
        console.log('selectedData: ', selectedData);
        this.contentsService.updateContent(this.engType[this.selectedData.type], selectedData)
            .subscribe(function (data) {
            _this.alertService.info((data) ? '컨텐츠가 수정되었습니다.' : '오류가 발생했습니다.');
            _this.updateContents(_this.selectedData.type);
            console.log(data);
        }, function (error) {
            _this.alertService.info('불러오기에 실패하였습니다.');
            console.log('error: ', error);
        });
    };
    return ContentsModificationComponent;
