import { OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { AccountRecordComponent } from '../../micro/account-record/account-record.component';
import { PostingService } from '../../services/posting.service';
import { PostItem } from '../../models/post-item';
import { TableComponent } from '../../micro/table/table.component';
import { MypostDirective } from '../../directives/mypost.directive';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
var AccountComponent = /** @class */ (function () {
    function AccountComponent(userService, postingService) {
        this.userService = userService;
        this.postingService = postingService;
        this.orderByLevel = 'ALL';
        this.orderByAmount = 'ALL';
        this.categoryName = [{
                'u-id': '아이디',
                'name': '이름',
                'email': '이메일',
                'level': '등급',
                'point': '포인트',
                'level-start': '사용 시작일',
                'level-end': '사용 종료일',
                'amount': '가용 자산'
            }];
    }
    AccountComponent.prototype.ngOnInit = function () {
        this.updateData(this.orderByLevel, this.orderByAmount);
    };
    AccountComponent.prototype.updateData = function (level, amount, id) {
        var _this = this;
        this.userService.getUserList(level, amount, id).subscribe(function (data) {
            _this.List = JSON.parse(data.list);
            console.log(_this.List);
            _this.total = _this.List.length;
            _this.addRecord(_this.List);
        });
    };
    AccountComponent.prototype.searchData = function (query) {
        var _this = this;
        this.userService.searchUser(query).subscribe(function (data) {
            _this.List = JSON.parse(data.list);
            console.log(_this.List);
            _this.total = _this.List.length;
            _this.addRecord(_this.List);
        });
    };
    AccountComponent.prototype.addRecord = function (records) {
        var ref = this.mypostDirective.viewContainerRef;
        ref.clear();
        // tslint:disable-next-line:forin
        for (var record in records) {
            console.log('record: ', records[record]);
            this.postingService.loadComponent(ref, new PostItem(AccountRecordComponent, records[record]));
        }
    };
    AccountComponent.prototype.viewPopup = function () {
        this.popupCsv.showPopup();
    };
    AccountComponent.prototype.changeLevel = function (event) {
        console.log('changedLevel: ', event);
        this.orderByLevel = event;
        this.updateData(this.orderByLevel, this.orderByAmount, 1);
    };
    AccountComponent.prototype.changeAmount = function (event) {
        console.log('changedAmount: ', event);
        this.orderByAmount = event;
        this.updateData(this.orderByLevel, this.orderByAmount, 1);
    };
    AccountComponent.prototype.changeQuery = function (event) {
        console.log('changedQuery: ', event.path[0].value);
        this.searchData(event.path[0].value);
    };
    AccountComponent.prototype.sendSMS = function () {
    };
    AccountComponent.prototype.sendMail = function () {
    };
    AccountComponent.prototype.download = function () {
        var name = 'We2Win_계정관리_' + (new Date().toISOString().slice(0, 10));
        // tslint:disable-next-line:no-unused-expression
        new Angular2Csv(Object.assign(this.categoryName, this.List), name);
    };
    return AccountComponent;
}());
export { AccountComponent };



