import { OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
var AnalysisUserRecordComponent = /** @class */ (function () {
    function AnalysisUserRecordComponent(userService) {
        this.userService = userService;
    }
    AnalysisUserRecordComponent.prototype.ngOnInit = function () {
        console.log('record in accountRecordComponent: ', this.record);
    };
    AnalysisUserRecordComponent.prototype.deleteUser = function () {
        if (confirm('정말로 해당 이용자를 삭제하시겠습니까?')) {
            this.userService.deleteUser(this.record);
        }
        // window.location.reload();
    };
    return AnalysisUserRecordComponent;
}());
export { AnalysisUserRecordComponent };



