import { OnInit } from '@angular/core';
import { UserService } from '../../../services/user.service';
import { PostingService } from '../../../services/posting.service';
import { PostItem } from '../../../models/post-item';
import { TableComponent } from '../../../micro/table/table.component';
import { MypostDirective } from '../../../directives/mypost.directive';
import { AnalysisUserRecordComponent } from '../../../micro/analysis-user-record/analysis-user-record.component';
import { Angular2Csv } from 'angular2-csv';
var AnalysisUserComponent = /** @class */ (function () {
    function AnalysisUserComponent(userService, postingService) {
        this.userService = userService;
        this.postingService = postingService;
        this.date = new Date();
        this.orderByLevel = 'ALL';
        this.orderByAmount = 'ALL';
    }
    AnalysisUserComponent.prototype.ngOnInit = function () {
        this.updateData(this.orderByLevel, this.orderByAmount);
    };
    AnalysisUserComponent.prototype.updateData = function (level, amount, id) {
        var _this = this;
        this.userService.getUserList(level, amount, id).subscribe(function (data) {
            _this.List = JSON.parse(data.list);
            console.log(_this.List);
            _this.total = _this.List.length;
            _this.addRecord(_this.List);
        });
    };
    AnalysisUserComponent.prototype.searchData = function (query) {
        var _this = this;
        this.userService.searchUser(query).subscribe(function (data) {
            _this.List = JSON.parse(data.list);
            console.log(_this.List);
            _this.total = _this.List.length;
            _this.addRecord(_this.List);
        });
    };
    AnalysisUserComponent.prototype.addRecord = function (records) {
        var ref = this.mypostDirective.viewContainerRef;
        ref.clear();
        // tslint:disable-next-line:forin
        for (var record in records) {
            console.log('record: ', records[record]);
            this.postingService.loadComponent(ref, new PostItem(AnalysisUserRecordComponent, records[record]));
        }
    };
    AnalysisUserComponent.prototype.viewPopup = function () {
        this.popupCsv.showPopup();
    };
    AnalysisUserComponent.prototype.changeLevel = function (event) {
        console.log('changedLevel: ', event);
        this.orderByLevel = event;
        this.updateData(this.orderByLevel, this.orderByAmount, 1);
    };
    AnalysisUserComponent.prototype.changeAmount = function (event) {
        console.log('changedAmount: ', event);
        this.orderByAmount = event;
        this.updateData(this.orderByLevel, this.orderByAmount, 1);
    };
    AnalysisUserComponent.prototype.changeQuery = function (event) {
        console.log('changedQuery: ', event.path[0].value);
        this.searchData(event.path[0].value);
    };
    AnalysisUserComponent.prototype.sendSMS = function () {
    };
    AnalysisUserComponent.prototype.sendMail = function () {
    };
    AnalysisUserComponent.prototype.download = function (type) {
        switch (type) {
            case 'all':
                var data = [
                    {
                        name: 'Test 1',
                        age: 13,
                        average: 8.2,
                        approved: true,
                        description: 'using Content here, content here '
                    },
                    {
                        name: 'Test 2',
                        age: 11,
                        average: 8.2,
                        approved: true,
                        description: 'using Content here, content here '
                    },
                    {
                        name: 'Test 4',
                        age: 10,
                        average: 8.2,
                        approved: true,
                        description: 'using Content here, content here '
                    },
                ];
                // tslint:disable-next-line:no-unused-expression
                new Angular2Csv(data, 'My Report');
                break;
        }
    };
    return AnalysisUserComponent;
}());
export { AnalysisUserComponent };



