import { OnInit } from '@angular/core';
import { ContentsService } from '../../services/contents.service';
import { PostingService } from '../../services/posting.service';
import { PostItem } from '../../models/post-item';
import { TableComponent } from '../../micro/table/table.component';
import { MypostDirective } from '../../directives/mypost.directive';
import { EmployerRecordComponent } from '../../micro/employer-record/employer-record.component';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { RecordService } from '../../services/record.service';
import { AlertService } from '../../services/alert.service';
var EmployerComponent = /** @class */ (function () {
    function EmployerComponent(contentsService, postingService, recordService, alertService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.recordService = recordService;
        this.alertService = alertService;
        this.selectedList = [];
        this.categoryName = [{
                'no': 'no.',
            }];
    }
    EmployerComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.getList();
        this.recordService.change.subscribe(function (data) {
            if (data.checked) {
                _this.selectedList.push(data['c-id']);
            }
            else {
                // remove c-id from selectedList
                var index = _this.selectedList.indexOf(data['c-id']);
                if (index !== -1) {
                    _this.selectedList.splice(index, 1);
                }
            }
        });
    };
    EmployerComponent.prototype.getList = function () {
        var _this = this;
        this.contentsService.getEmployerList().subscribe(function (data) {
            _this.List = data;
            console.log('list: ', _this.List);
            _this.total = _this.List.length;
            _this.addRecord(_this.List);
        });
    };
    EmployerComponent.prototype.addRecord = function (records) {
        var ref = this.mypostDirective.viewContainerRef;
        ref.clear();
        // tslint:disable-next-line:forin
        for (var record in records) {
            console.log('record: ', records[record]);
            this.postingService.loadComponent(ref, new PostItem(EmployerRecordComponent, records[record]));
        }
    };
    EmployerComponent.prototype.confirm = function () {
        var _this = this;
        this.contentsService.confirmEmployers(this.selectedList, true).subscribe(function (data) {
            _this.alertService.success('승인되었습니다.');
            _this.selectedList = [];
            _this.getList();
        });
    };
    EmployerComponent.prototype.deny = function () {
        var _this = this;
        this.contentsService.confirmEmployers(this.selectedList, false).subscribe(function (data) {
            _this.alertService.success('승인 해제되었습니다.');
            _this.selectedList = [];
            _this.getList();
        });
    };
    EmployerComponent.prototype.viewPopup = function () {
        this.popupCsv.showPopup();
    };
    EmployerComponent.prototype.download = function () {
        var name = 'We2Win_구인_' + (new Date().toISOString().slice(0, 10));
        // tslint:disable-next-line:no-unused-expression
        new Angular2Csv(this.List, name);
    };
    return EmployerComponent;
}());
export { EmployerComponent };



