import { OnInit, ViewContainerRef } from '@angular/core';
import { ContentsService } from '../../services/contents.service';
import { ActivatedRoute } from '@angular/router';
import { PostingService } from '../../services/posting.service';
var DashboardComponent = /** @class */ (function () {
    function DashboardComponent(viewContainerRef, contentsService, postingService, route) {
        this.viewContainerRef = viewContainerRef;
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.route = route;
        this.date = new Date();
        this.data = {
            'users': {},
            'contents': {},
            'logs': {},
        };
    }
    DashboardComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.contentsService.getDashboardUsers().subscribe(function (data) {
            console.log('users: ', data);
            _this.data['users'] = data['users'];
        }, function (error) {
            console.error(error);
        });
        this.contentsService.getDashboardContents().subscribe(function (data) {
            console.log('contents: ', data);
            _this.data['contents'] = data['contents'];
            _this.data['logs'] = data['logs'];
        }, function (error) {
            console.error(error);
        });
        setTimeout(function () {
            _this.addData(_this.infoPie);
        }, 500);
    };
    DashboardComponent.prototype.addData = function (component) {
        console.log(component.chartObject);
        component.chartObject.data.datasets[0].data = [0, 0, 5];
        // component.chartObject.data.labels = [
        //     'PLATINUM',
        //     'PREMIUM',
        //     'STANDARD'
        // ];
        component.chartObject.update();
    };
    return DashboardComponent;
}());
export { DashboardComponent };



