import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Headers, RequestOptions } from '@angular/http';
// yimport { Observable } from 'rxjs/Observable';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
var InfoService = /** @class */ (function () {
    function InfoService(http) {
        this.http = http;
    }
    InfoService.prototype.getAll = function () {
        return this.http.get('/api/v1/infos');
    };
    InfoService.prototype.getById = function (id) {
        return this.http.get('/api');
    };
    InfoService.prototype.create = function (data) {
        var bodyString = data;
        // const bodyString = JSON.stringify(data);
        console.log('bodyString: ', bodyString);
        var headers = new Headers({ 'Content-Type': 'application/json' });
        var options = new RequestOptions({ headers: headers });
        return this.http.post('http://ec2-13-125-222-53.ap-northeast-2.compute.amazonaws.com/api/v1/infos', bodyString);
        // .map((res: User) => JSON.stringify(res))
        // .catch((error: any) => Observable.throw(error.message));
    };
    InfoService.prototype.update = function (user) {
        return this.http.put('/api', user);
    };
    InfoService.prototype.delete = function (id) {
        return this.http.delete('/api');
    };
    InfoService.prototype.try = function () {
        return this.http.get('/api');
    };
    InfoService.prototype.handleError = function (error) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        }
        else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error("Backend returned code " + error.status + ", " +
                ("body was: " + error.error));
        }
        // return an ErrorObservable with a user-facing error message
        return new ErrorObservable('Something bad happened; please try again later.');
    };
    return InfoService;
}());
export { InfoService };



